@import url(https://fonts.googleapis.com/css2?family=Handlee&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.todo-list__todo--done .todo__text--underlined {
  text-decoration: line-through;
}

.todo-list__button-remove--hidden {
  display: none;
}

.todo-list__todo {
  -webkit-order: 3;
          order: 3;
}

.todo-list__todo--done {
  -webkit-order: 5;
          order: 5;
  background: grey;
}

.todo-list__todo, .todo-list__todo--done {
  font-family: 'Handlee', cursive;
  margin: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border: 1px solid;
  display: inline-block;
  word-break: break-all;
}

.todo-list__button-done, .todo-list__button-remove {
  text-decoration: none;
}
.todo-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.todo-list__form {
  background: red;
  -webkit-order: 1;
          order: 1;
  padding: 2rem;
}

.pending {
  -webkit-order: 2;
          order: 2;
}

.completed {
  -webkit-order: 4;
          order: 4;
  background: lightgray;
}

.pending, .completed {
  border-top: 3px solid;
}

.todo-list__clear-button {
  -webkit-order: 6;
          order: 6;
  margin: 1rem;
  width: 25%;
  margin: 2rem auto;
}

.App {
  font-family: 'Kanit', sans-serif;
  text-align: center;
  border-bottom: none;
  margin: 1rem;
  box-shadow: 3px 3px 3px 3px gray;
  border-bottom-right-radius: 25px;
  border-top-left-radius: 25px;
}

.header {
  margin: 0;
  background: blue;
  color: white;
  border-top-left-radius: 25px;
}

.header__title {
  margin: 0;
}

.todo-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

@media screen and (min-width: 600px) {
  #root {
    padding-top: 1rem;
  }

  .App {
    padding-top: 3rem;
    margin: auto;
    width: 75%;
    padding: 10px;
  }
}
